.stock {
  margin-top: -190px;
  text-align: center;
  background: url('./assets/background/boxes-left.svg'), url('./assets/background/boxes-right.svg') {
    repeat: no-repeat;
    position: left top 50px, right top 50px; }

  &__p {
    max-width: 450px;
    margin: 0 auto 14px; }

  &__grid {
    display: grid;
    position: relative;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 19px;
    grid-auto-rows: minmax(100px, auto); }

  &__daily {
    overflow: hidden;
    position: relative;
    grid-column: 2;
    grid-row: 1 / 3;
    padding: 25px 30px;
    text-align: left;
    background: linear-gradient(115deg, rgba(253, 206, 125, 1), rgba(175, 112, 57, 1), rgba(175, 112, 57, 1), rgba(253, 206, 125, 1));
    box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15); }

  &__price {
    font-family: "Rotonda Bold", sans-serif;
    font-size: 9.6em;
    line-height: 123px;
    font-weight: 700;
    color: #ffffff;

    &--s {
      display: inline-block;
      font-size: 6.4em;
      margin-top: -40px;
      line-height: 82px; } }

  &__char {
    position: absolute;
    max-width: 208px;
    left: 30px;
    bottom: 18px; }

  &__icon {
    position: absolute;
    bottom: -41px;
    right: -41px;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    background: #ffffff;

    img {
      position: absolute;
      width: 122px;
      bottom: 28%;
      left: 22%; } } }
