.products {
  margin-top: -150px;
  padding: 250px 0 200px;
  min-height: 800px;
  text-align: center;
  background: url('assets/background/product__back--top.png'), url('assets/background/product__back--top.png'), url('assets/background/product__back--bottom.png'), url('assets/background/product__back--bottom.png'), linear-gradient(135deg, rgba(253, 206, 125, 1), rgba(175, 112, 57, 1), rgba(175, 112, 57, 1), rgba(253, 206, 125, 1)) {
    repeat: no-repeat;
    position: top, top, bottom, bottom, top;
    size: 100% 282px, 100% 384px, 100% 199px,100% 271px, 100% 100%; }

  &__list {
    display: flex;
    justify-content: space-between;
    margin: 109px 0 20px;
    gap: 19px; }

  &__buy {
    max-width: 421px;
    margin: 0 auto; } }
